// name "Student","Teacher","Group" .       
// object  ,, 
const Validate = (name, object) => {
    const temp_errors = {};
    const regPhone = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;
    for(var key in object){
        //    
        if(object[key] === undefined || object[key].length === 0){
            //  &  &  
            if (key === 'name') {
                temp_errors[key] = ' .';
            }  else if (key === 'phone') {
                temp_errors[key] = ' .';
            }  else if (key === 'officeHours') {
                temp_errors[key] = ' .';
            }        

            // 
            else if (key === 'school') {
                temp_errors[key] = ' .';
            } else if (key === 'parents') {
                temp_errors[key] = '  .';
            } else if (key === 'grade') {
                temp_errors[key] = ' .';
            } else if (key === 'category') {
                temp_errors[key] = ' .';
            } else if (key === 'subjects') {
                temp_errors[key] = ' .';
            }

            // 
            else if ( key === 'subject') {
                temp_errors[key] = ' .';
            }

            // 
            else if (key === "teacher"){
                temp_errors[key] = ' .';
            } else if (key === 'students'){
                temp_errors[key] = ' .';
            }
        }

        //  
        if (key === 'phone' && object[key].length != 0 && regPhone.test(object[key]) === false) {
            temp_errors[key] = '   .';
        }
    
        if (key === 'parents' && object[key].length != 0 && regPhone.test(object[key]) === false) {
        temp_errors[key] = '    .';
        }    

        //       
        if (key === 'subjects') {
            for (var i = 0; i < object[key].length; i++) {
                if (!object[key][i].teacher) {
                    temp_errors[key] = object[key][i].name + '  .';
                }
            }
        }

        //       
        if (key === 'officeHours' && name === "Group") {
            for (var i = 0; i < object[key].length; i++) {
                if (object[key].length != 0 && object[key][i].times.length === 0) {
                    temp_errors[key] = object[key][i].day + '  .';
                }
            }
        }
    }

    // temp_error   true,
    //    temp_errors . 
    if (Object.keys(temp_errors).length === 0 && temp_errors.constructor === Object) {
        return true;
    } else {
        return temp_errors;
    }
}

export default Validate;